.button-wrapper {
	height: 6rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-weight: 100;
	font-size: 1.4rem;
	background: #e0e1e6;
	color: #888888;
	-ms-flex:1 1;
	    flex:1 1;
	outline: 1px solid #888888;
	cursor: pointer;
}
.operator {
	background-color: #fe9241;
	color: #ffffff;
}


